.projects-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.page-header {
  margin-bottom: 40px;
}

.page-header h2 {
  font-size: 2.2rem;
  color: #0f172a;
  margin-bottom: 10px;
}

.page-description {
  color: #475569;
  font-size: 1.1rem;
  line-height: 1.6;
  max-width: 800px;
}

.projects-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 40px;
}

.project-card {
  background: white;
  border-radius: 12px;
  border: 1px solid #e2e8f0;
  overflow: hidden;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.project-card:hover {
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.project-card.expanded {
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
}

.project-summary {
  padding: 24px;
  cursor: pointer;
}

.project-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 15px;
}

.project-title-section {
  display: flex;
  align-items: center;
  gap: 15px;
  flex: 1;
}

.project-title-section h3 {
  font-size: 1.5rem;
  color: #0f172a;
  margin: 0;
}

.project-status {
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  color: white;
  text-transform: capitalize;
}

.project-progress {
  display: flex;
  align-items: center;
  gap: 15px;
  flex: 1;
  max-width: 300px;
}

.progress-bar {
  flex: 1;
  height: 8px;
  background: #e2e8f0;
  border-radius: 4px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  border-radius: 4px;
  transition: width 0.5s ease;
}

.progress-text {
  font-weight: 600;
  color: #475569;
  min-width: 50px;
  text-align: right;
}

.toggle-btn {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: 2px solid #e2e8f0;
  background: white;
  color: #475569;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  margin-left: 15px;
}

.toggle-btn:hover {
  border-color: #cbd5e1;
  background: #f8fafc;
}

.project-description {
  color: #64748b;
  line-height: 1.6;
  margin: 0;
  padding-top: 15px;
  border-top: 1px solid #f1f5f9;
}

.project-details {
  border-top: 1px solid #e2e8f0;
  background: #f8fafc;
  padding: 30px;
}

.placeholder {
  text-align: center;
  padding: 40px 20px;
  color: #64748b;
}

.placeholder ul {
  list-style: none;
  padding: 0;
  margin: 20px auto;
  max-width: 400px;
  text-align: left;
}

.placeholder li {
  margin-bottom: 8px;
  padding-left: 24px;
  position: relative;
}

.placeholder li::before {
  content: 'â†’';
  position: absolute;
  left: 0;
  color: #3b82f6;
}

.projects-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  margin-top: 40px;
}

.stat-card {
  background: white;
  border-radius: 12px;
  padding: 24px;
  border: 1px solid #e2e8f0;
  display: flex;
  align-items: center;
  gap: 20px;
  transition: transform 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
}

.stat-icon {
  font-size: 2.5rem;
}

.stat-content h4 {
  margin: 0 0 8px 0;
  color: #0f172a;
  font-size: 1.2rem;
}

.stat-content p {
  margin: 0;
  color: #64748b;
  font-size: 0.95rem;
}

@media (max-width: 768px) {
  .project-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }

  .project-progress {
    max-width: 100%;
    width: 100%;
  }

  .toggle-btn {
    position: absolute;
    top: 24px;
    right: 24px;
  }

  .project-title-section {
    width: 100%;
  }

  .projects-stats {
    grid-template-columns: 1fr;
  }
}
